export default {
	data() {
		return {
			lib: '',
			answerText: '',
			isXiugai: false,
			answerTestArea: '',
			changeIndex: 0
		}
	},
	onLoad(option) {
		let self = this
		uni.getStorage({
			key: 'test',
			success: function (res) {
				console.log(res);
				if(res.data instanceof Array && res.data.length > 0){
					console.log(res.data)
					self.lib = res.data
					self.answerText = res.data[parseInt(option.index)].answer
					self.answerTestArea = res.data[parseInt(option.index)].answer
					self.changeIndex = parseInt(option.index)
				}
			}
		});
	},
	methods: {
		changeAnswer() {
			this.isXiugai = true
		},
		changeFinish() {
			this.isXiugai = false
			this.answerText = this.answerTestArea
			this.lib[this.changeIndex].answer = this.answerTestArea
			uni.setStorage({
			    key: 'test',
			    data: this.lib,
			    success: function () {
			        console.log('success');
			    }
			});
		}
	}
}
